<template>
    <div class="home">
        <div class="swipe">
            <van-swipe @change="onChange" :autoplay="3000">
                <van-swipe-item v-for="(item,index) in bannerData" :key="index">
                    <img :src="item.bannerImg" class="auto-img">
                    <div class="banner-name">{{item.name}}</div>
                </van-swipe-item>
            </van-swipe>

            <div class="userInfo-box" @click="login">
                <div class="padding">
                    <div class="logo iconfont icon-kafei"></div>
                    <div class="userInfo">
                        <div class="tips" v-if="!isLogin">您好，请登录</div>
                        <div class="tips" v-else>{{nickName}}</div>
                        <div class="proVip" v-if="!isLogin">加入大咖会 享会员惊喜特权</div>
                        <div class="tips" v-else>欢迎光临！</div>
                    </div>
                    <div class="ins" v-if="!isLogin">></div>
                </div>
            </div>
        </div>

        <div class="adress-box">
            <div class="iconfont icon-dizhi"></div>
            <div class="adress">深圳华润大厦分店</div>
            <div class="distance">336m</div>
            <div class="more">更多门店 ></div>
        </div>

        <div class="rec-box">
            <div class="rec">
                <div class="title">
                    <div class="kaziti">咖自提</div>
                    <div class="zhekou">全场8折</div>
                </div>
                <div class="desc">预约点餐 优惠省时</div>
                <div class="go">GO ></div>
                <div class="rec-img">
                    <img src="../assets/images/smallImg1.jpg">
                </div>
            </div>
            <div class="rec">
                <div class="title">
                    <div class="kaziti">咖速达</div>
                </div>
                <div class="desc">即时送，享美味</div>
                <div class="go">GO ></div>
                <div class="rec-img">
                    <img src="../assets/images/smallImg2.jpg">
                </div>

            </div>
        </div>

        <div class="guessLike">
            <div class="guessLike-txt">
                <span class="split">——</span>
                猜您喜欢
                <span class="split">——</span>
            </div>
            <div class="shop-item-box">
                <div class="shop-item" v-for="(item,index) in hotProduct" :key="index" @click="goDetail(item.pid)">
                   <div class="padding">
                        <div class="shop-item-img">
                            <img :src="item.smallImg" alt="">
                        </div>
                        <div class="shop-desc">
                            <div class="shop-item-name">{{item.name}}</div>
                            <div class="shop-item-oldPrice">￥28.00 <span class="manjian">满减</span> </div>
                            <div class="shop-item-price">￥{{item.price}}</div>
                        </div>
                    </div>
                </div>
                
            </div>
        </div>
    </div>
</template>

<script>
    import '../assets/less/home.less'

    export default {
        data() {
            return {
                current: 0,
                bannerData:[],
                hotProduct: [],
                // 是否登录成功
                isLogin: false,
                // 用户名
                nickName: ''
            };
        },
        created(){
            // 轮播图数据
            this.getBannerData();
            // 热卖商品数据
            this.getHotProduct();
            //判断是否登录过
            this.isLoginSuccessful();
        },
        methods: {
            // 修改轮播图索引
            onChange(index) {
                this.current = index;
            },
            // 获取轮播图数据
            getBannerData(){
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0.
                });
                this.axios({
                    method: "GET",
                    url: "/banner",
                    params: {
                        appkey: this.appkey,
                    }
                }).then(res => {
                    this.$toast.clear();
                    if(res.data.code == 300){
                        this.bannerData = res.data.result;
                    }
                }).catch(err => {
                    this.$toast.clear();
                    
                })
            },

            // 获取热卖商品数据
            getHotProduct(){
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0.
                });
                this.axios({
                    method: "GET",
                    url: "/typeProducts",
                    params: {
                        appkey: this.appkey,
                        key: 'isHot',
                        value: 1
                    }
                }).then(res => {
                    this.$toast.clear();
                    if(res.data.code == 500){
                        this.hotProduct = res.data.result;
                    }
                }).catch(err => {
                    this.$toast.clear();
                })
            },
            goDetail(pid){
                this.$router.push({name:'Detail',params:{pid}})
            },
            isLoginSuccessful(){
                let tokenString = localStorage.getItem('token');
                if(!tokenString){
                    this.$toast('请登录！');
                    return;
                }
                this.axios({
                    method: "GET",
                    url: '/findMy',
                    params: {
                        appkey: this.appkey,
                        tokenString,
                    }
                }).then(res => {
                    if(res.data.code == "A001"){
                        this.nickName = res.data.result[0].nickName;
                        // 
                        this.isLogin = true;
                    }
                }).catch(err => {
                    
                })
            },
            //登录
            login(){
                if(!this.isLogin){
                    this.$router.push('/Login');
                }
                
            }
        },
    }
</script>
<style lang="less" scoped>
     /deep/.van-swipe{
            height: 270px;
        }
</style>
